import { Component, OnInit } from '@angular/core';

import { AlertService } from 'src/app/api/controllers/Alert';

@Component({
  selector: 'app-security-emergency-contact-numbers',
  templateUrl: './security-emergency-contact-numbers.component.html',
  styleUrls: ['./security-emergency-contact-numbers.component.scss']
})
export class SecurityEmergencyContactNumbersComponent implements OnInit {
  
  user:any;

  emergencyList:any;

  isContactsLoaded:boolean = false;

  constructor(
  	private alertService:AlertService
  ) { }

  ngOnInit() {


  	this.alertService.getAllAlertEmergencyNumbers().subscribe((res:any) => {

  	  this.isContactsLoaded = true;

      this.emergencyList = res;


    },
    error => {
      console.log(error);
    });

  }

}
